.userdashboards {
    background-color: $col22; 
    min-height: calc(100vh - 146px); 
    overflow: hidden; 
    .left_sidebar {
        .inner_side {
            background: $col18;  
            box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.05);
            margin-bottom: 15px; 

            .border-bottom {

                & > div.position-relative {
                    & > img {
                        max-width: 45px; 
                    }
                }

                & > div {
                    
                    & + div {
                        width: 100%; 
                    }
                }

                .position-relative {
                    & > div {
                        word-break: break-all; 
                    }
                }
            }
        }
        .chat-bg {
            background-color: $col23; 
            height: 3.5rem; 
            line-height: 3.5rem;
        }
        .chat-border {
            height: 1px; 
            background: $col35; 
        }
        .show-more {
            background: $col36;  
            text-align: center;
        }
        .border-bottom {
            border-bottom: solid 1px $col31; 
            padding-bottom: 10px !important;
            span.online {
                display: inline-block;
                width: 8px;
                height: 8px;
                background: $col32;
                position: absolute;
                top: 5px;
                right: 4px;
                border-radius: 50%; 
            }
            span.offline {
                display: inline-block;
                width: 8px;
                height: 8px;
                background: $col33;
                position: absolute;
                top: 5px;
                right: 4px;
                border-radius: 50%; 
            }
            .cross_btn {
                position: absolute;
                top: 0;      
                right: 0;
            }
            &:last-child {
                border-bottom: none !important;  
            }
            &:nth-child(8) { 
                border-bottom: none !important;  
            }
            
        }
    }

    .user_body { 
        .inner_body  { 
            background: linear-gradient(180deg, $col37 0%, $col18 100%); 
            box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.05);

            .ant-progress-inner:not(.ant-progress-circle-gradient) .ant-progress-circle-path {
                stroke: $col38;
                stroke-width: 12px; 
            }

            .ant-progress-circle-trail {
                stroke: $col53;
                stroke-width: 12px; 
            }

            &.userbg-white {
                background: $col18; 
            }

            @keyframes loading-1 {
                0% {
                    -webkit-transform: rotate(0deg);
                    transform: rotate(0deg);
                }
                100% {
                    -webkit-transform: rotate(180deg);
                    transform: rotate(180deg);
                }
            }
            @keyframes loading-2 {
                0% {
                    -webkit-transform: rotate(0deg);
                    transform: rotate(0deg); 
                }
                100% {
                    -webkit-transform: rotate(0deg); 
                    transform: rotate(0deg); 
                }
            }
            @keyframes loading-3 {
                0% {
                    -webkit-transform: rotate(0deg);
                    transform: rotate(0deg);
                }
                100% {
                    -webkit-transform: rotate(0deg);
                    transform: rotate(0deg); 
                }
            }
            @keyframes loading-4 { 
                0% {
                    -webkit-transform: rotate(0deg);
                    transform: rotate(0deg);
                }
                100% {
                    -webkit-transform: rotate(36deg);
                    transform: rotate(36deg);
                }
            }
           
            .progress {
                width: 101px;
                height: 101px;
                line-height: 101px;
                background: none;
                margin: 0 auto;
                box-shadow: none;
                position: relative;
                &:after {
                    content: "";
                    width: 100%;
                    height: 100%;
                    border-radius: 50%;
                    border: 10px solid $col53; 
                    position: absolute;
                    top: 0;
                    left: 0;
                }
                >span {
                    width: 50%;
                    height: 100%;
                    overflow: hidden;
                    position: absolute;
                    top: 0;
                    z-index: 1;
                }
                .progress-left {
                    left: 0;
                    .progress-bar {
                        left: 100%;
                        border-top-right-radius: 80px;
                        border-bottom-right-radius: 80px;
                        border-left: 0;
                        -webkit-transform-origin: center left;
                        transform-origin: center left;
                    }
                }
                .progress-bar {
                    width: 100%;
                    height: 100%;
                    background: none;
                    border-width: 10px;
                    border-style: solid;
                    position: absolute;
                    top: 0;
                }
                .progress-right {
                    right: 0;
                    .progress-bar {
                        left: -100%;
                        border-top-left-radius: 80px;
                        border-bottom-left-radius: 80px;
                        border-right: 0;
                        -webkit-transform-origin: center right;
                        transform-origin: center right;
                        animation: loading-1 1.8s linear forwards;
                    }
                }
                .progress-value {
                    width: 90%;
                    height: 90%;
                    border-radius: 50%;
                    background: $col18;
                    font-size: $fs25;
                    color: $col47;  
                    line-height: 92px;  
                    text-align: center;
                    position: absolute;
                    top: 5%;
                    left: 5%;
                }
            }

            .progress.yellow {
                .progress-bar {
                    border-color: $col38;  
                }
                .progress-left {
                    .progress-bar {
                        animation: loading-3 1.5s linear forwards 1.8s;
                    } 
                }
            }

            .progress.blue {
                .progress-bar {
                    border-color: $col38; 
                }
                .progress-left {
                    .progress-bar {
                        animation: loading-2 1.5s linear forwards 1.8s;
                    } 
                }
            }
            
            
            
        
            @media only screen and (max-width: 990px) { 
                .progress {
                    margin-bottom: 20px;
                }
            }

        }

        .border_yellow {
            border-top: solid 10px $col38; 
            padding: 25px;
        }
        .border-gray {
            border-bottom: solid 1px $col39; 
            line-height: 30px;
        }
    }

    .right_sidebar { 
        box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.05);  
        .chat-bg {
            background-color: $col23; 
            height: 3.5rem; 
            line-height: 3.5rem;
        }
        .chat-pink {
            background-color: $col7;  
            height: 3.5rem; 
            line-height: 3.5rem;
        }
        .right_inner_side {  
            background: $col18; 
            box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.05);
            margin-bottom: 15px; 
            .p-3 {
                div.d-flex.mt-4.form-group {
                    & > input.inputTyp4 {
                        max-width: 200px; 
                    }
                }
            }
            .bg-grays {
                border-bottom: solid 1px $col31; 
                overflow: hidden;
            }

            .border-bottom { 

                
                border-bottom: solid 1px $col31; 
                padding-bottom: 10px !important;    
                img {
                    max-width: 45px; 
                }
                
                &:last-child {
                    border-bottom: none !important;  
                }
                &:nth-child(8) { 
                    border-bottom: none !important;  
                }
                
            }
        }
    }

}

.adminsidebar {  
    .inner_area {
        .chat-bg {
            background-color: $col23; 
            height: 3.5rem; 
            line-height: 3.5rem;
        }
        
        background: $col18; 
        box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.05); 
        min-height: 100vh;

        .border-bottom {

            .position-relative {
                
                & > div {
                    & > img {
                        display: none;
                    }
                }
            }

            div {
                
                & > div {
                    & > img {
                        display: none;
                    }

                    & > a {
                        color: $col23;
                        text-decoration: none; 
                    }
                }
            }
 
            & > div {
                cursor: pointer;

                & > div {
                    color: $col28 !important;
                }
            }

            & div.active {
                & > div {
                    color: $col23 !important;
                }
            }
        }

    }
    
}

.adminlistener {  
    background: $col18; 
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.05); 

    div & img { 
        max-width: 67px; 
    }

    .d-flex.justify-content-between { 
        & div {
            & > div {
                word-break: break-all; 
                padding-right: 15px;
                cursor: pointer;
            }
        }
        
        .mb-auto.d-flex {
            & > span {
                cursor: pointer; 
            }
        }
    } 

    .custom-switch .custom-control-label {
        cursor: pointer; 
    }

    .custom-switch .custom-control-label::before {   
        width: 34px;
        height: 14px;
        background: $col55;
        border-color: $col55;
    }

    .custom-switch .custom-control-input:checked~.custom-control-label::after {
        background: linear-gradient(51.19deg, rgba(218, 255, 104, 0.5) 31.92%, rgba(135, 244, 173, 0.5) 97.68%),
         $col58; 
        box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.1);
        border-radius: 50%;
        width: 20px;
        height: 20px;
        top: calc(.25rem + -4px);
        box-shadow: none !important; 
    }

    .custom-switch .custom-control-label::after {
        background: linear-gradient(0deg, $col56, $col56), $col57;
        box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.1);
        border-radius: 50%;
        width: 20px;
        height: 20px;
        top: calc(.25rem + -4px);
        box-shadow: none !important;
    }

}

