        margin: auto;
        ul.pagination {
            justify-content: flex-end;
            margin-top: 10px; 
            margin-right: 15px;
            li {
                border: solid 1px $col57; 
                padding: 10px;
                width: 45px;
                text-align: center;
                margin-right: -1px;  

                & > a {
                    color: $col28; 
                    text-decoration: none;
                }

                &.active {
                    background: $col23;
                    border: solid 1px $col23; 

                    & > a {
                        color: $col18;  
                    }
                }
            }
        }
    }
}

